<script>
import EchartsBar from "./controller/EchartsBar";
import EchartsGeo from "./controller/EchartsGeo";
import D3Chart from "./controller/D3Chart";
import { mapGetters } from "vuex";
export default {
  name: "EchatsStory",
  components: {
    D3Chart,
  },
  data: () => {
    return {};
  },
  computed: {
    ...mapGetters("mapView", ["currentPage", "step"]),
    renderEchart() {
      switch (this.step) {
        case "3-0":
          return "D3Chart";
        case "3-1":
          return "D3Chart";
        case "3-2":
          return "D3Chart";
        case "3-3":
          return "EchartsGeo";
        case "3-4":
          return "EchartsGeo";
        case "3-5":
          return "EchartsGeo";
        case "3-6":
          return "EchartsGeo";
        case "3-7":
          return "EchartsGeo";
        case "3-8":
          return "EchartsGeo";
        case "3-9":
          return "EchartsGeo";
        case "3-10":
          return "EchartsGeo";
        case "3-11":
          return "EchartsGeo";
        case "3-12":
          return "EchartsGeo";
        case "3-13":
          return "EchartsGeo";
        default:
          return "D3Chart";
      }
    },
  },
  mounted() {
    this.initScrollama();
  },
  methods: {
    initScrollama() {
      const ScrollamaMapSecond = this.$scrollama();
      ScrollamaMapSecond.setup({
        step: "#EchartsContainer .scrolly-text .step",
        offset: 64 / window.innerHeight,
      })
        .onStepEnter(this.handleStepEnter)
        .onStepExit((response) => {
          //console.log(response);
        });
    },
    handleStepEnter(response) {
      const currentStep = response.element.children[0].dataset["step"];
      this.$store.dispatch("mapView/updateStep", currentStep);
      //console.log("this enter " + currentStep);
    },
  },
};
</script>
